.navbar {
  background-color: #B24C63;
  margin-bottom: 0;
}
.navbar a,
.navbar p {
  color: white;
}
.navbar .nav > li > a:hover,
.navbar .nav > li > a:focus {
  color: #B0DAF1;
  background-color: inherit;
}
.navbar .navbar-header .fa-bars {
  color: white;
  font-size: 2em;
}
.navbar .navbar-header .navbar-toggle {
  margin-top: 0;
  margin-bottom: 0;
}
.navbar .navbar-header .fa-bars:hover,
.navbar .navbar-header .fa-bars:focus {
  color: #B0DAF1;
}
.navbar .navbar-header .navbar-brand:hover,
.navbar .navbar-header .navbar-brand:focus {
  color: #B0DAF1;
  background-color: inherit;
}
html {
  position: relative;
  min-height: 100%;
}
body {
  /* Margin bottom by footer height */
  margin-bottom: 60px;
}
footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 60px;
  background-color: #f5f5f5;
}
footer .container {
  background-color: #f5f5f5;
}
footer p {
  padding-left: 15px;
  padding-right: 15px;
}
.center-text {
  text-align: center;
}
.text-1 {
  color: #B24C63;
}
.text-1 a:hover,
.text-1 a:focus {
  color: #c26f82;
}
.text-2 {
  color: #B0DAF1;
}
.text-2 a:hover,
.text-2 a:focus {
  color: #dbeef9;
}
.text-white {
  color: white;
}
.text-white a:hover,
.text-white a:focus {
  color: #e6e6e6;
}
.bg-1 {
  color: white;
  background-color: #B24C63;
}
.bg-1 a:hover,
.bg-1 a:focus {
  background-color: #c26f82;
}
.bg-2 {
  color: white;
  background-color: #B0DAF1;
}
.bg-2 a:hover,
.bg-2 a:focus {
  background-color: #dbeef9;
}
